import "./styles.css";
import PersonInfo from './componants/PersonData'
import Card from './componants/Card'
import Navbar from './componants/Navbar'
export default function App() {

  const Persons = PersonInfo.map((person,index) =>{
    return <Card key={index}
                 {...person}
           />
  })

  return (
    <div className="App">
    <Navbar />
      {
        Persons
      }
    </div>
  );
}
