import React from "react";

export default function Card(props) {
  let status;
  if (props.active === 0) {
    status = "Offline";
  } else if (props.active === 1) {
    status = "online";
  }

  return (
    <>
      <div className="card-container">
        <img src={`../images/${props.coverImg}`} alt="objectImage" />
        <div className="text-content">
          <p>
            <img
              className="locaIcon"
              src={`../images/icon8Loca.png`}
              alt="objectImage"
            />
            {props.location}
            {<span className="active">{status}</span>}
          </p>
          <h1>{props.heading}</h1>
          <p>{props.para}</p>
        </div>
      </div>
    </>
  );
}
